.App {
    font-family: sans-serif;
    text-align: center;
}
html {
    background-color: #919DFF;
}

@media(max-width: 800px) {
    h1 {
        text-align: center;
        font-size: 25px;
    }
    canvas {
        position: absolute;
        top: 0;
        transform: translate(-32%, 7%);
    }
}
@media(max-width: 1800px) {
    h1 {
        font-size: 45px;
    }
    canvas {
        transform: translate(-30%, 7%);
        width: 1200px;
    }
}


  
